.app-container {
  min-height: 100vh;
  padding: 0 10px;
  background-image: url(./assets/img/mainWallp.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.app-header {
  max-width: 1400px;
  width: 100%;
  margin: 0 auto;
  padding: 30px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.app-main {
  max-width: 1400px;
  width: 100%;
  margin: 0 auto;
}
